@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row {
    background-color: #fff;
    color: #25213b;
    display: flex;
    /* flex-flow: row nowrap; */
    justify-content: space-evenly;
    height: 60px;
    padding: 0.6em 0 0.6em 0;
    color: #575763;
    box-shadow: inset 0px -1px 0px rgba(183, 183, 183, 0.25);
}
.mini-span {
    width: 3em;
    flex: 4 1;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
}
.mini-span-small {
    width: 3em;
    flex: 1 1;
}
.span-name {
    font-weight: bold;
    flex: 5 1;
    color: #25213B;

}
.main-span {
    flex: 10 1;
    width: 15em;
    display: flex;
    flex-flow: column;
    align-items: center;
}
.desc {
    align-items: flex-start;
}
input {
    background-color: #9f9f9f;
    width: 0.8em;
}
.issues, .forks, .gazers {
    font-weight: bold;
    padding: 0.2em;
    border-radius: 0.3em;
    align-items: flex-start;
    /* align-items: center; */
}
.invalid {
    color: #8C70FF;
    background-color: #F6F3FF;
}
.valid {
    color: #F4B400;
    background-color: #FFF6DE;
} 
.row-heading {
    color: #899198;
    font-weight: bold;
    text-transform: uppercase;
    background: #F3F6F9;
    margin-bottom: -1.5em;
}
.low {
    color: #3AB65C;
    background-color: #F6F3FF;
}
.high {
    color: #F4B400;
    background-color: #FFF6DE;
}
.hide {
    visibility:hidden;
}
.zero {
    color: #3AB65C;
    background-color: #F6F3FF;
}
.count {
    color: #00A2D6;
    background-color: #F6F3FF;
}
 .board {
     width: 75vw;
     font-size: 0.9em;
     font-family: 'Open Sans', sans-serif;     
     border-radius: 0.5em;
     box-shadow: 0.3em 0.3em 0.2em rgb(210, 214, 214);
     box-shadow: 0.3em 0.3em 0.2em rgb(233, 236, 236);
    }
.no-repo {
    font-size: 1.5em;
    color: #9696a0;    
    font-family: 'Quicksand', sans-serif;    
    /* font-family: 'Open Sans', sans-serif;    box-shadow: 0.3em 0.3em 0.2em rgb(210, 214, 214); */
}

.tag {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 25em;
    font-size: 1.3em;
    border-radius: 0.5em;
    padding: 0.3em;
    margin: 0.5em;
    background-color: #fff;
    box-shadow: 0.3em 0.3em 0.2em rgb(233, 236, 236);
}

.tag-left {
    display: flex;
    flex-flow: column;
}
.tag-right {
    margin-top: 2em;
    margin-right: 1em;
    color: #dedee6;
}
.slide-count {
    font-size: 2em;
    font-weight: 700;
    color: #19191d;
    box-shadow: none;
}
.slide-tag {
    font-size: 1em;
    font-weight: bold;
    color: #9696a0;
    box-shadow: none;
}
.banner {
    width: 75vw;
    font-size: 0.9em;
    border-radius: 0.5em;
    font-family: 'Open Sans', sans-serif;    
    margin-bottom: 1em;
}
.dashboard-top {
    display: flex;
    justify-content: space-around;
  }


img {
    width: 4em;
    height: 4em;
    border-radius: 50%;
}
.profile {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: flex-start;
    width: 21em;
    font-size: 0.7em;
    line-height: 1.5em;
    border-radius: 0.5em;
    padding: 0.5em;
    margin: 0.5em;
    background-color: #fff;
    color: #838797;
    box-shadow: none;
    box-shadow: 0.3em 0.3em 0.2em rgb(233, 236, 236);
}
p {
    margin: 0.4em 0em;
    color: #3d3d49;
    font-size: 1.2em;
    font-weight: 700;
    font-family: 'Quicksand', sans-serif;    box-shadow: none;
    display: flex;
    box-shadow: none;
}
span {
    padding-left: 0.5em;
}
.username {
    font-size: 1.8em;
}
.user-banner {
    margin-left: 2em;
    margin-top: 1em;
}
a {
    text-decoration: none;
}
.date-header {
    display: flex;
    justify-content: space-between;
    width: 17em;
}
.date-day {
    display: flex;
    justify-content: space-between;
    font-size: 2.5em;
    font-weight: bold;
    line-height: 1.2em;
}
.date-month-year {
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    font-size: 0.4em;
    line-height: 0.6em;
}
.weekday {
    margin-top: 0.7em;
    font-weight: 900;
    font-size: 1.3em;
}
p {
    margin: 0.4em 1em;
    color: #3d3d49;
    font-size: 1.1em;
    box-shadow: inset 0px -1px 0px rgba(183, 183, 183, 0.25);
}
.todo-item {
    width: 15em;
    padding: 0.5em;
    display: flex;
    justify-content: space-between;
}
.done {
    text-decoration: line-through;
}
img {
    width: 4em;
    height: 4em;
    border-radius: 50%;
}
.todos {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: flex-start;
    width: 22em;
    font-size: 0.7em;
    font-family: 'Roboto', sans-serif;    
    border-radius: 0.5em;
    line-height: 1em;
    /* padding: 0.3em; */
    margin: 0.5em;
    background-color: #fff;
    color: #545454;
    box-shadow: 0px 4px 30px rgba(192, 192, 192, 0.25);
    box-shadow: 0.3em 0.3em 0.2em rgb(233, 236, 236);
}
.todo-dateheader {
    margin: 1em 1em;
    font-size: 1.1em;
}
.add-todo {
    position: relative;
    display: flex;
    justify-content: flex-end;
    right: -15em;
}

.usernavigation {
    position: relative;
    font-size: 1.5em;
    display: flex;
    justify-content: space-between;
    width: 5em;
    /* color: #72809d; */
    padding-top: 0.5em;
    align-self: flex-end;
    left: 90%;
}
.user-avatar {
    color: #72809d;
    padding-left: 0.25em;
}
.user-action {
    margin-left: 0.1em;
    margin-top: -0.2em;
    cursor: pointer;
}
.user-logout {
    cursor: pointer;
}
.logout {
    position: absolute;
    top: 1.8em;
    right: 0.5em
}
/* .user-notification { */
    /* margin-top: 0.2em; */
/* } */
.todo-form {
    /* position: relative; */
    display: flex;
    flex-flow: column;
    font-family: 'Roboto', sans-serif;    
    box-shadow: none;
}
.todo-form-header {
    font-size: 1.4em;
    font-weight: bolder;
    /* padding-left: -0.9em; */
}
.todo-form-tip {
    font-size: 0.8em;
    margin-bottom: 1.3em;
}
.todo-form span {
    margin-bottom: 0.4em;
    font-weight: 300;
}
.close-div {
    display: flex;
    width: 20em;
}
.close-btn {
    position: relative;
    right: 1em;
}
input[type='text'] {
    width: 20em;
    padding: 0.8em;
    margin-top: 0.9em;
    background-color: #fff;
    color: #000;
    border: none;
    font-family: 'Roboto', sans-serif;
    font-size: 0.8em;
    border-bottom: 0.01em solid rgb(184, 180, 180); 
}
input[type='text']:focus {
  outline: none;
}

.todo-input {
    border: none;
}
.btn {
    padding: 0.3em;
    border-radius: 0.3em;
    align-self: flex-end;
    margin-left: 0.1em;
}
.btn:hover {
    cursor: pointer;
}
.btn-span {
    margin-top: 0.3em;
    font-size: 0.9em;
    margin-left: 1em;
}
.add {
    color: rgb(68, 140, 182);
}
.cancel {
    color: rgb(135, 138, 139);
}
.app {
    text-align: center;
    /* background-color: #e5e5e5; */
    background-color: #faf8f8;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    padding: 1em;
    margin: 0;
    width: 100%;
  }
  .top-content {
    display: flex;
    width: 95%;
  }
  h2 {
    color: #00A2D6;
    background-color: #F6F3FF;
    border-radius: 0.3em;
    padding: 0.3em;
    margin-bottom: -0.2em;
    margin-top: 0.1em;
    font-family: 'Quicksand', sans-serif;    
  }
  .app-left {
    font-size: 1.15em;
    margin-right: -2em;
  }
  .refresh {
    position: -webkit-sticky;
    position: sticky;
    font-weight: bold;
    text-align: center;
    padding-top: 3em;
    margin: 0 0 0 32em;
  }
input {
    width: 87%;
    font-size: 0.9em;
    margin: 0;
    border: none;
    padding: 0.8em;
    border-radius: 0.3em;
    border-bottom: 1px solid gray;
}
input[type='password'] {
    font-size: 0.9em;
    border-bottom: 1px solid gray;
    background-color: #fff;
}
input:focus {
    border: none;
    outline: none;
    /* border-bottom: 1px solid rgb(227, 239, 248); */
}
input[type='password']:focus {
    border: none;
    outline: none;
}

.input_section {
    background-color: #fff;
    border-radius: 0.3em;
    color: #000;
    width: 80%;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
}
button {
    padding: 0.5em;
    margin-bottom: 1em;
    margin-left: 1em;
    border-radius: 0.5em;
    font-size: 0.9em;
    font-weight: bold;
    color: #fff;
    border: none;
    cursor: pointer;
}

.btn {
    color: #fff;
    /* margin-right: 9em; */
    padding: 0.5em;
    background-color: rgb(21, 78, 105);
}

.btn:hover {
    background-color: rgb(97, 87, 87);
}

button[disabled] {
    background-color: gray;
}

button[disabled]:hover {
    background-color: gray;
}
.homepage-banner {
    /* width: 75vw; */
    font-size: 0.9em;
    border-radius: 0.5em;
    background-color: rgb(136, 133, 133);
    font-family: 'Open Sans', sans-serif;    
    /* box-shadow: 0.3em 0.3em 0.2em rgb(210, 214, 214); */
    margin-bottom: 1em;
}
.homepage-top {
    display: flex;
    justify-content: space-around;
    background-color: rgba(177, 171, 171, 0.295);
    margin: 3em auto;
    padding: 3em auto;
    width: 80vw;
    height: 80vh;
    border-radius: 0.5em;
  }
  .tag-home {
    position: relative;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    width: 23em;
    font-size: 1.3em;
    border-radius: 0.5em;
    padding: 0.3em;
    margin: 2.5em;
    background-color: #fff;
    box-shadow: 0.3em 0.3em 0.2em rgb(233, 236, 236);
}

.slide-home {
    font-size: 1.6em;
    font-weight: 700;
    color: #19191d;
    box-shadow: none;
    margin-bottom: 1em;
}
.slide-home {
    font-size: 1em;
    font-weight: bolder;
    color: #1f1f22;
    box-shadow: none;
    margin-bottom: 1em;
}
.mode {
    padding: 0.2em;
    font-size: 0.8em;
    cursor: pointer;
    border-radius: 0.2em;
    margin: 0 0.3em;
    color: #fff;
    background-color: rgb(22, 99, 88);
}
.tc {
    width: 18em;
    font-size: 0.8em;
    display: flex;
    margin-left: -6em;
    margin-top: 1em;
}
.tc span {
    margin-top: -0.2em;
    margin-left: -0.5em;
}
.tc-red {
    color: red;
}
.tc .input_section {
    margin-right: 0;
    margin-bottom: 1em;
    width: 2em;
}
.btn {
    margin-left: -2em;
    margin-right: 11em;
}


