.homepage-banner {
    /* width: 75vw; */
    font-size: 0.9em;
    border-radius: 0.5em;
    background-color: rgb(136, 133, 133);
    font-family: 'Open Sans', sans-serif;    
    /* box-shadow: 0.3em 0.3em 0.2em rgb(210, 214, 214); */
    margin-bottom: 1em;
}
.homepage-top {
    display: flex;
    justify-content: space-around;
    background-color: rgba(177, 171, 171, 0.295);
    margin: 3em auto;
    padding: 3em auto;
    width: 80vw;
    height: 80vh;
    border-radius: 0.5em;
  }
  .tag-home {
    position: relative;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    width: 23em;
    font-size: 1.3em;
    border-radius: 0.5em;
    padding: 0.3em;
    margin: 2.5em;
    background-color: #fff;
    box-shadow: 0.3em 0.3em 0.2em rgb(233, 236, 236);
}

.slide-home {
    font-size: 1.6em;
    font-weight: 700;
    color: #19191d;
    box-shadow: none;
    margin-bottom: 1em;
}
.slide-home {
    font-size: 1em;
    font-weight: bolder;
    color: #1f1f22;
    box-shadow: none;
    margin-bottom: 1em;
}
.mode {
    padding: 0.2em;
    font-size: 0.8em;
    cursor: pointer;
    border-radius: 0.2em;
    margin: 0 0.3em;
    color: #fff;
    background-color: rgb(22, 99, 88);
}
.tc {
    width: 18em;
    font-size: 0.8em;
    display: flex;
    margin-left: -6em;
    margin-top: 1em;
}
.tc span {
    margin-top: -0.2em;
    margin-left: -0.5em;
}
.tc-red {
    color: red;
}
.tc .input_section {
    margin-right: 0;
    margin-bottom: 1em;
    width: 2em;
}
.btn {
    margin-left: -2em;
    margin-right: 11em;
}

